body {
  color: var(--main-dark-txt-cl);
  text-transform: uppercase;
  background-color: var(--main-light-bg);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  font-size: 0;
}

picture {
  font-size: 0;
  display: block;
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  padding: 0;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.section {
  padding-top: 40px;
  padding-bottom: 80px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-bottom: 100px;
  }
}

@media screen and (min-width: 1440px) {
  .section {
    padding-top: 40px;
    padding-bottom: 72px;
  }
}

.section-title {
  letter-spacing: -.02em;
  font-size: 40px;
  font-weight: 500;
  line-height: 1;
}

@media screen and (min-width: 768px) {
  .section-title {
    font-size: 68px;
  }
}

.main-btn {
  height: 18px;
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: var(--black-cl);
  border: 2px solid var(--btn-border-cl);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  padding: 14px 23px;
  font-family: DM Sans;
  font-weight: 700;
  line-height: 1.29;
  display: flex;
}

@media screen and (max-width: 374.98px) {
  .main-btn {
    font-size: 14px;
    line-height: 1.28;
  }
}

@media screen and (min-width: 768px) {
  .main-btn {
    height: 64px;
    padding: 20px 150px;
    font-size: 18px;
    line-height: 1.3;
  }
}

@media screen and (min-width: 1440px) {
  .main-btn {
    line-height: 1.333;
  }
}

.main-btn:hover, .main-btn:focus {
  color: var(--white-cl);
  background-color: var(--hover-light-cl);
}





.categories-list {
  height: 228px;
  width: 335px;
  flex-direction: column;
  gap: 14px;
  margin: 40px 0;
  padding-right: 28px;
  display: inline-flex;
  overflow-y: scroll;
}

.categories-list::-webkit-scrollbar {
  width: 8px;
  border-radius: 12px;
}

.categories-list::-webkit-scrollbar-thumb {
  height: 114px;
  background: #e0e0e0;
  border-radius: 12px;
}

.categories-list li a {
  text-transform: capitalize;
  letter-spacing: -.02em;
  color: rgba(17, 17, 17, .6);
  padding: 5px 0;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-decoration: none;
  transition: color .3s ease-in;
}

.categories-list li a:hover {
  letter-spacing: -.02em;
  text-transform: uppercase;
  color: #4f2ee8;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

.book_temp {
  width: 335px;
  height: 485px;
  object-fit: cover;
  border-radius: 8px;
}

.category_title_last_word {
  letter-spacing: -.04em;
  color: #4f2ee8;
  font-family: DM Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
}

.outlineli {
  width: 335px;
}

.title_categories {
  letter-spacing: -.04em;
  color: #111;
  font-family: DM Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
}

.js_books_view {
  flex-direction: column;
  row-gap: 40px;
  padding-top: 40px;
  display: flex;
}

.adition-category-title {
  letter-spacing: .03em;
  text-transform: uppercase;
  color: #b4afaf;
  margin-bottom: 18px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.book-info {
  padding-top: 14px;
  padding-bottom: 8px;
}

.book-info-title {
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: #111;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

.book-info-autor {
  letter-spacing: -.04em;
  color: #b4afaf;
  font-family: DM Sans;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

.load-more-books {
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: #111;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  flex-direction: row;
  align-items: flex-start;
  gap: 10px;
  margin: 0 0 0 auto;
  padding: 14px 28px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.list-books li:nth-child(n+2) {
  display: none;
}

@media (min-width: 768px) {
  .list-books {
    gap: 25px;
    display: flex;
  }

  .categories-list {
    height: 472px;
    width: 337px;
    gap: 22px;
  }

  .list-books li:nth-child(-n+3) {
    display: block;
  }

  .book_temp {
    width: 218px;
    height: 316px;
  }

  .outlineli {
    width: 218px;
  }

  .js_books_view {
    flex-flow: wrap;
    gap: 40px 25px;
    display: flex;
  }

  .category-books {
    padding-bottom: 0;
  }
}

@media (min-width: 1440px) {
  .categories-list {
    height: 472px;
    width: 356px;
    gap: 22px;
    display: inline-flex;
  }

  .list-books li:nth-child(n) {
    display: block;
  }

  .book_temp {
    width: 180px;
    height: 256px;
  }

  .outlineli {
    width: 180px;
  }

  .list-books {
    gap: 24px;
    display: flex;
  }

  .categories-list li a:hover {
    font-size: 15px;
  }

  .wrapper-categories-box {
    max-width: 990px;
    flex-direction: column;
    margin-left: 40px;
    display: inline-flex;
  }

  .js_books_view {
    flex-wrap: wrap;
    gap: 40px 24px;
    display: flex;
  }

  .js_books_view li, .list-books li {
    flex-basis: calc(20% - 19.2px);
  }

  .category-books {
    padding-bottom: 0;
  }
}







